.derecha {
	display: flex;
	flex-direction: column;
	align-items: center;
	@include tablet {
		flex-flow: row nowrap;
	}
	.dark-mode-boton {
		width: 3rem;
		filter: invert(100%);
		cursor: pointer;
		@include tablet {
			margin-left: 2rem;
		}
	}
}

.barra {
	display: flex;
	flex-direction: column;
	padding-top: 3rem;
	align-items: center;

	@include tablet {
		flex-direction: row;
		justify-content: space-between;
	}
}

.navegacion {
	visibility: hidden;
	opacity: 0;
	height: 0;
	overflow: hidden;
	transition: visibility 0.3s ease-in-out, opacity 0.4s ease-out, height 2s ease-out;
	@include tablet {
		overflow: visible;

		opacity: 1;
		display: flex;
		align-items: center;
		visibility: visible;
	}
	a {
		color: $blanco;
		font-size: 2.2rem;
		display: block;
		text-align: center;
		&:hover {
			color: $verde;
		}
		@include tablet {
			margin-right: 2rem;
			font-size: 1.8rem;
			&:last-of-type {
				margin-right: 0;
			}
		}
	}
}
/* Menu hamburguesa */
.mobile-menu {
	@include tablet {
		display: none;
	}
	img {
		width: 5rem;
	}
}
.ver {
	visibility: visible;
	opacity: 1;
	height: auto;
}
